
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { fuseAnimations } from '@fuse/animations';
import { FuseNavigationService } from '@fuse/components/navigation/navigation.service';
import { UtilityService } from 'app/shared/services/utility/utility.service';
/**
 * @title Basic use of the tab group
 */
@Component({
  selector: 'app-charger-type-crud',
  templateUrl: './charger-type-crud.component.html',
  styleUrls: ['./charger-type-crud.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})
export class ChargerTypeCrudComponent implements OnInit {

  tabLinks = [
    // { label: 'Charger Models', link: 'list' },
    // { label: 'Add New', link: 'create' }
  ];
  menuArray: any;
  test: any;
  localStorage: any;
  objectData: any;
  objectChildData: any;
  tempLinks = [];
  childData: any;
  constructor(private fuseNavigationService: FuseNavigationService, private router: Router,
    private cdr: ChangeDetectorRef, private _utilityService: UtilityService) {
  }

  ngAfterViewInit() {
    this._utilityService.activeMenuItems$.subscribe(menuItemSelecte => {
      this.menuArray = menuItemSelecte;
      if (this.menuArray == null || this.menuArray == undefined || this.menuArray.title == null || this.menuArray.title == undefined) {
        this.menuArray = JSON.parse(localStorage.getItem('activeMenuItems'));
      }
      this.cdr.detectChanges();
    })
  }

  ngOnInit() {
    this.fuseNavigationService.sysMenuItemSelectedValue$.subscribe(menuItemSelecte => {
      this.test = menuItemSelecte;
    })
    //this.menuArray = this.test //JSON.parse( localStorage.getItem('activeMenuItems'));
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    let nav_list = this.localStorage.nav_list_with_tab;
    nav_list.filter((x) => {
      if (x.id === "assetManagement") {
        this.objectData = x;
        if (this.objectData.children != undefined && this.objectData.children != null) {
          this.objectData.children.filter((y) => {
            if (y.id === "chargerTypeManagement") {
              this.objectChildData = y;
              if (this.objectChildData.children != undefined && this.objectChildData.children != null) {
                this.objectChildData.children.filter((z) => {
                  this.childData = z;
                  if (z.id === "crud") {
                    let isDefaultRoutePermeted = false;
                    if (this.childData.children != undefined && this.childData.children != null) {
                      this.childData.children.filter((t) => {
                        if (t.id === "chargerModels") {
                          isDefaultRoutePermeted = true;
                          this.tempLinks.push({ label: 'Charger Models', link: 'list' });
                        }
                        if (t.id === "addNew") {
                          this.tempLinks.push({ label: 'Add New', link: 'create' });
                        }
                      });
                    }

                    this.tabLinks = this.tempLinks;
                    if (!isDefaultRoutePermeted) {
                      this.router.navigate(['/assetManagement/chargerTypeManagement/crud/' + this.tempLinks[0].link]);
                    }
                  }
                });
              }


            }

          });
        }

      }
    });

  }
}




@Component({
  selector: 'sunny-routed-content',
  template: 'Having a lot of light from the sun.',
})
export class SunnyTabContentComponent { }

@Component({
  selector: 'rainy-routed-content',
  template: 'A rainy period of time is one when it rains a lot',
})
export class RainyTabContentComponent { }

@Component({
  selector: 'foggy-routed-content',
  template: 'If the weather is foggy, there is fog',
})
export class FoggyTabContentComponent { }

