import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TABS_DEMO_ROUTES } from './charger-type-crud/routes';
import { ChargerTypeCrudComponent } from './charger-type-crud/charger-type-crud.component';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : ChargerTypeCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    }
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class ChargerTypeRoutingModule{}