import { Component, OnInit,ViewChild } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { MasterService } from 'app/shared/services/master/master.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ChargerTypeService } from 'app/shared/services/charger-type/charger-type.service';
import {MessageConstant} from 'app/shared/utils/message.constant';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations
})
export class CreateComponent implements OnInit {
 
  form: FormGroup;
  brands:any;
  models:any;
  chargerTypes:any;
  vehicleTypes:any;
  localStorage : any;
  messageConstant:MessageConstant;
  @ViewChild("toggleElement") ref: any;
  status = false;
  constructor(private fb: FormBuilder, private masterService : MasterService,
    private chargerTypeService : ChargerTypeService,private snackBar: MatSnackBar,
    private router : Router) {
       
  }

  ngOnInit() {
    this.initialize();
  }

  initialize(){

    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));

    this.form = this.fb.group({
      name: ['', [Validators.required,Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      description: ['', [Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      status: [{value:false},  [Validators.required]]
    });
   
  }

  onSubmit(){
    if(this.form.valid){
      let formValues = this.form.value;
    
      let params = {
        name :formValues.name, 
        description :formValues.description, 
        status :  this.status == true ? 'Y' : 'N', 
        created_by : this.localStorage.userId 
      }
      
      this.chargerTypeService.create(params).subscribe(
        (resp:any )=>{
          
          if(resp != null){
            this.snackBar.open(MessageConstant.chargerCreateMessage, 'Dismiss', { duration: 5000 });
            this.router.navigate(['/assetManagement/chargerTypeManagement/crud/list']);
          } 
        },
        (err)=>{
          this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
        }
      )
    }else{
      this.snackBar.open(MessageConstant.formInvalid, 'Dismiss', { duration: 3000 });
    }
    

  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
      ? 'Not a valid email'
      : '';
  }
  updateStatus(event){
    this.status = event.checked;
    this.ref.checked = this.status;
  }
}


